*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .post-input{
        padding:48px 4px;
        input{
            background-color: #fcf8f3;
            width: 80%;
            height:120px;
            border-radius: 10px;
            font-size: 18px;
        }
        input:hover{
            box-shadow:5px 10px 18px #99d3f1;
        }
    }
    
}
.post{
    width:40%;
    border: 1px solid #888888;
    padding:8px 8px;
    .post-user{
        display: flex;
        align-items: center;
        .post-user_img{
            margin:0 8px;
            img{
                width: 48px;
                height: 48px;
                border-radius:50%;
                border: 2px solid #888888;
            }
        }
        h5{
            font-weight: bold;
            color: #283747;
            font-family:sans-serif;
        }
    }

    .post-comment{
        p{
            color: #283747;
            letter-spacing: 1.25px;
            line-height: 1.5;
            font-size: 1rem;
        }
    }

    .post-detail{
        display: flex;
        justify-content: space-around;
        .post-detail-tools{
            color: #99d3f1;
        }
        .post-detail-reaction{
            color: #888888;
        }
        .post-detail__icon{
            width:28px;
            height:28px;
            font-size:24px;
            margin: 0 8px;
            cursor: pointer;
        }
        .fa-edit:hover,.fa-comment:hover{
            color: #007bff;
        }
        .fa-trash-alt:hover,.fa-heart:hover{
            color: #f02849;
        }
        .fa-retweet:hover{
            color:#45bd62;
        }
        .time{
            font-weight: 500;
        }
        .like{
            font-weight:bold;
            color: #f02849;
        }
    }
    .post-edit-input{
        width: 100%;
        height:120px;
        background-color: #fcf8f3;
        border-radius: 10px;
        border:none;
        padding:16px;
    }
    .post-edit-input:hover{
        box-shadow:5px 10px 18px #99d3f1;
    }
    .post-edit-btn{
        margin-top: 16px;
        .btn+.btn{
            margin-left:16px;
        }
    }
}

@media (min-width:740px) and (max-width: 1094px) {
    .post{
        width: 60%;
    }
}
@media (max-width: 739px) {
    .post{
        width: 95%;
    }
}